.contenedorPrincipal{
  width: 100%;
  height: calc(100vh - 100px);
  color: azure;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 100px;
}

h1{
  font-size: 3em;
  font-weight: 600;
}

.contenedorFormulario{
  width: 50%;
  display: flex;
  justify-content: center;
  margin: 5px 0;
  padding: 10px;
  border-radius: 10px;
}

.contenedorTareas{
  width: 50%;
  background-color: rgb(41, 41, 41);
  padding: 10px;
  margin-top: 5px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

input{
  line-height: 30px;
  margin: 0 10px;
  border: none;
  border-radius: 5px;
}

.btn{
  background: linear-gradient(45deg, #ff8d0c, #ff1153);
  color: #ffff;
  cursor: pointer;
  padding: 0 20px;
  border: none;
  border-radius: 10px;
  transition: all 0.5;
  margin-right: 10px;
}



.btn:hover{
  background-color: white;
  transform: scale(1.1);
  font-weight: 600;
}





.contenedorInfoTareas {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.contenedorTarea{
  width: 80%;
  margin: 10px 0;
  padding: 20px;
  font-size: 1.3em;
  display: flex;
  justify-content: center;
  border-radius: none;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 0px 30px;
}

.todoTarea{
  margin-right: 50px;
}

.spanSubrayado {
  text-decoration: line-through;
}

